.cart__bottom h6 span {
  font-size: 1.1rem;
  font-weight: 600;
}

.cart__bottom button {
  border: none;
  padding: 6px 30px;
  border-radius: 25px;
  background: #fff;
  color: #212245;
}

.cart__bottom button a {
  color: #212245;
  font-weight: 600;
}

@media only screen and (min-width: 870px) {
  .cart {
    width: 25%;
  }
}

.cart__closeButton {
  height: 60px;
  padding: 10px 20px;
  position: absolute;
  top: 0.5rem;
  right: 0rem;
  margin: 0.5rem 0;
  display: none;
}

.cart__closeButton span i {
  font-size: 1.1rem;
  background: #212245;
  padding: 5px;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
